
.w {
  width: 1200px;
  margin: 0 auto;
}
* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
a:hover {
    color: #00aeff;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clearfix::after {
  content: "";
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#app .header {
    line-height: 80px;
    overflow: hidden;
}
#app .header .logo {
      width: 80px;
      height: 80px;
}
#app .header .logo img {
        width: 100%;
}
#app .header .website-name {
      margin-left: 20px;
      color: #00aeff;
}
#app .header .nav-list {
      text-align: right;
}
#app .header .nav-list li {
        float: left;
}
#app .header .nav-list li a {
          display: block;
          padding: 0 22px;
          color: #888;
}
#app .header .nav-list li a:hover {
            color: #00aeff;
}
#app .footer {
    height: 260px;
    background: #f5f5f5;
    text-align: left;
    padding: 30px 0;
}
#app .footer dl dt {
      font-size: 20px;
      color: #333;
      font-weight: 700;
      margin-bottom: 20px;
}
#app .footer dl dd {
      color: #666;
      line-height: 28px;
      font-size: 14px;
}

.pg-index .index-container {
  text-align: center;
  padding: 100px 0;
}
.pg-index .index-container h2 {
    font-size: 36px;
}
.pg-index .index-container p {
    margin: 60px 0;
    color: #00aeff;
    font-size: 28px;
}
.pg-index .index-container .block-list li {
    float: left;
    width: 30%;
    margin: 0 20px;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.pg-index .index-container .block-list li:hover {
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
}
.pg-index .index-container .block-list li .item {
      height: 440px;
      border: 1px solid #e8e8e8;
      border-radius: 5px;
      position: relative;
}
.pg-index .index-container .block-list li .item .img {
        width: 160px;
        height: 160px;
        margin: 60px auto;
}
.pg-index .index-container .block-list li .item .img img {
          width: 100%;
}
.pg-index .index-container .block-list li .item .name {
        margin: 0;
        font-size: 18px;
        color: #1f2f3d;
        font-weight: 400;
}
.pg-index .index-container .block-list li .item .desc {
        font-size: 14px;
        color: #99a9bf;
        padding: 0 25px;
        line-height: 20px;
        margin: 20px 0;
}
.pg-index .index-container .block-list li .item .details {
        height: 53px;
        line-height: 52px;
        font-size: 14px;
        color: #409eff;
        text-align: center;
        border: 0;
        border-top: 1px solid #eaeefb;
        padding: 0;
        cursor: pointer;
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: #fff;
        border-radius: 0 0 5px 5px;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
        text-decoration: none;
        display: block;
}
.pg-index .index-container .block-list li .item .details:hover {
          background: #00aeff;
          color: #fff;
}

.demo-container {
  -webkit-transition: max-height .3s ease;
  transition: max-height .3s ease;
  overflow: hidden;
}
.docs-demo {
  width: 100%;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
  background-color: #F7F7F7;
  border: 1px solid #e2ecf4;
  border-top: none;
}
.docs-demo pre code {
    font-family: Consolas,Menlo,Courier,monospace;
    line-height: 22px;
    border: none;
}
.docs-trans {
  width: 100%;
  text-align: center;
  display: inline-block;
  color: #C5D9E8;
  font-size: 12px;
  padding: 10px 0;
  background-color: #FAFBFC;
}
.docs-demo__code,
.highlight-wrapper,
.docs-demo__meta {
  padding: 0 20px;
  overflow-y: auto;
}
.docs-demo__code {
  border-bottom: 1px solid #eee;
}
.docs-demo.docs-demo--expand .docs-demo__meta {
  border-bottom: 1px dashed #e9e9e9;
}
.docs-demo.docs-demo--expand .docs-demo__triangle {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.highlight-wrapper {
  display: none;
}
.highlight-wrapper p,
  .highlight-wrapper pre {
    margin: 0;
}
.highlight-wrapper .hljs {
    padding: 0;
}
.docs-demo.docs-demo--expand .highlight-wrapper {
  display: block;
}
.docs-demo__code__mobi {
  height: 620px;
  margin: 20px 0;
}
.docs-demo__code__mobi__header {
  border-radius: 4px 4px 0 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(55, 55, 55, 0.98)), to(#545456));
  background: linear-gradient(rgba(55, 55, 55, 0.98), #545456);
  text-align: center;
  padding: 8px;
}
.docs-demo__code__mobi__header img {
    width: 100%;
}
.docs-demo__code__mobi__header .url-box {
    height: 28px;
    line-height: 28px;
    color: #fff;
    padding: 0 3px;
    background-color: #a2a2a2;
    margin: 10px auto 0;
    border-radius: 4px;
    white-space: nowrap;
    overflow-x: auto;
}
.docs-demo__code__mobi__content iframe {
  width: 100%;
  border: 0;
  height: 548px;
}
